<template>
  <label class="pan-checkbox" :class="{'is-checked': isChecked}">
    <span class="pan-checkbox__input">
      <span class="pan-checkbox__inner"></span>
      <input
        type="checkbox"
        class="pan-checkbox__original"
        :name="name"
        v-model="val"
        :value="label"
      >
    </span>
    <span class="pan-checkbox__label">
      <slot>{{label}}</slot>
    </span>
  </label>
</template>

<script>
export default {
  name: 'panCheckbox',
  inject: {
    checkboxGroup: {
      default: ''
    }
  },
  props: {
    label: {
      type: String,
      default: ''
    },
    name: {
      type: String,
      default: ''
    },
    value: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    val: {
      get () {
        return this.isGroup ? this.checkboxGroup.value : this.value
      },
      set (value) {
        this.isGroup ? this.checkboxGroup.$emit('input', value) : this.$emit('input', value)
      }
    },
    isGroup () {
      return !!this.checkboxGroup
    },
    isChecked () {
      // 如果有group包裹，判断Label是否在val中
      // 如果没有group包裹，直接使用val
      return this.isGroup ? this.val.includes(this.label) : this.val
    }
  }
}
</script>

<style lang="scss">
.pan-checkbox {
  color: #606266;
  font-weight: 500;
  font-size: 14px;
  position: relative;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  user-select: none;
  margin-right: 30px;
  .pan-checkbox__input {
  white-space: nowrap;
  cursor: pointer;
  outline: none;
  display: inline-block;
  line-height: 1;
  position: relative;
  vertical-align: middle;
  .pan-checkbox__inner {
    display: inline-block;
    position: relative;
    border: 1px solid #dcdfe6;
    border-radius: 2px;
    box-sizing: border-box;
    width: 14px;
    height: 14px;
    background-color: #fff;
    z-index: 1;
    transition: border-color .25s cubic-bezier(.71,-.46,.29,1.46),background-color .25s cubic-bezier(.71,-.46,.29,1.46);
    &:after {
    box-sizing: content-box;
    content: "";
    border: 1px solid #fff;
    border-left: 0;
    border-top: 0;
    height: 7px;
    left: 4px;
    position: absolute;
    top: 1px;
    transform: rotate(45deg) scaleY(0);
    width: 3px;
    transition: transform .15s ease-in .05s;
    transform-origin: center;
    }
  }
  .pan-checkbox__original {
    opacity: 0;
    outline: none;
    position: absolute;
    margin: 0;
    width: 0;
    height: 0;
    z-index: -1;
  }
  }
  .pan-checkbox__label {
  display: inline-block;
  padding-left: 10px;
  line-height: 19px;
  font-size: 14px;
  }
}
.pan-checkbox.is-checked {
    .pan-checkbox__input {
      .pan-checkbox__inner {
        border-color: #409eff;
        background-color: #409eff;
        &:after {
        transform: rotate(45deg) scaleY(1);
        }
      }
    }
    .pan-checkbox__label {
      color: #409eff;
    }
}
</style>
